<script setup lang="ts">
import { ref, computed, onMounted, inject } from 'vue'
import { 
  Upload, 
  FileText, 
  Clock, 
  CheckCircle2, 
  User, 
  Building2,
  Save,
  Send,
  Download,
  ArrowLeft,
  ArrowRight,
  AlertCircle
} from 'lucide-vue-next'
import { rfpUseToast } from '@/composables/rfpUseToast'

const { success, error: showError } = rfpUseToast()

// Get token from injection
const token = inject('token')

// Loading and error states
const loading = ref(true)
const error = ref(null)

const formData = ref({
  // Basic vendor info
  companyName: "",
  contactName: "",
  email: "",
  phone: "",
  website: "",
  
  // Additional vendor details
  legalName: "",
  businessType: "",
  incorporationDate: "",
  taxId: "",
  dunsNumber: "",
  industrySector: "",
  annualRevenue: "",
  employeeCount: "",
  headquartersCountry: "",
  headquartersAddress: "",
  description: "",
  riskLevel: "",
  status: "",
  onboardingDate: "",
  experienceYears: "",
  rating: "",
  location: "",
  
  // Arrays for capabilities and certifications
  capabilities: [],
  certifications: []
})

const responses = ref({})

const completionStatus = ref({
  company: 0,
  responses: 0,
  documents: 0,
  personnel: 0
})

const evaluationCriteria = ref([])

const documents = ref([
  { name: "Company Profile", uploaded: false, required: true },
  { name: "Technical Certifications", uploaded: false, required: true },
  { name: "Reference Letters", uploaded: false, required: true },
  { name: "Insurance Certificate", uploaded: false, required: false },
  { name: "Financial Statements", uploaded: false, required: true }
])

const keyPersonnel = ref([])

const overallProgress = computed(() => {
  return Math.round(
    (completionStatus.value.company + completionStatus.value.responses + completionStatus.value.documents + completionStatus.value.personnel) / 4
  )
})

const handleAutoSave = () => {
  success("Progress saved automatically")
}

// Load RFP data when component mounts
onMounted(async () => {
  try {
    // Check if we have a token
    console.log('VendorPortal: Using token:', token)

    if (!token) {
      error.value = 'Invalid access. Please use a valid invitation link or RFP access link.'
      console.error('No token found in props or URL')
      return
    }

    // Load RFP and invitation data
    const response = await fetch(`/api/v1/rfp/invitation/${token}/`)
    if (!response.ok) {
      throw new Error('Failed to fetch RFP data')
    }
    
    const data = await response.json()
    console.log('API Response:', data)
    
    // Update vendor info from both invitation and vendor data
    const vendor = data.vendor
    formData.value = {
      // Basic info from invitation
      companyName: data.invitation.company_name || vendor?.company_name || '',
      contactName: data.invitation.vendor_name || '',
      email: data.invitation.vendor_email || '',
      phone: data.invitation.vendor_phone || '',
      website: vendor?.website || '',
      
      // Additional vendor details
      legalName: vendor?.legal_name || '',
      businessType: vendor?.business_type || '',
      incorporationDate: vendor?.incorporation_date || '',
      taxId: vendor?.tax_id || '',
      dunsNumber: vendor?.duns_number || '',
      industrySector: vendor?.industry_sector || '',
      annualRevenue: vendor?.annual_revenue ? vendor.annual_revenue.toLocaleString() : '',
      employeeCount: vendor?.employee_count || '',
      headquartersCountry: vendor?.headquarters_country || '',
      headquartersAddress: vendor?.headquarters_address || '',
      description: vendor?.description || '',
      riskLevel: vendor?.risk_level || '',
      status: vendor?.status || '',
      onboardingDate: vendor?.onboarding_date || '',
      experienceYears: vendor?.experience_years || '',
      rating: vendor?.rating || '',
      location: vendor?.location || '',
      
      // Arrays for capabilities and certifications
      capabilities: vendor?.capabilities?.map(c => c.capability_name) || [],
      certifications: vendor?.certifications || []
    }
    
    // Update RFP info
    const rfp = data.rfp
    document.title = `${rfp.rfp_title} - RFP Portal`
    
    // Update RFP badge
    const rfpBadge = document.querySelector('.badge')
    if (rfpBadge) {
      rfpBadge.textContent = rfp.rfp_number
    }
    
    // Update RFP title and description
    const title = document.querySelector('.title')
    if (title) {
      title.textContent = rfp.rfp_title
    }
    
    // Update deadline and budget
    const deadlineText = document.querySelector('.deadline')
    if (deadlineText) {
      deadlineText.textContent = `Deadline: ${new Date(rfp.submission_deadline).toLocaleDateString()}`
    }
    
    const budgetText = document.querySelector('.budget')
    if (budgetText) {
      budgetText.textContent = `Budget: ${rfp.currency} ${rfp.budget_range_min?.toLocaleString()} - ${rfp.budget_range_max?.toLocaleString()}`
    }
    
    // Update evaluation criteria
    evaluationCriteria.value = data.evaluation_criteria.map(criteria => ({
      id: criteria.criteria_id.toString(),
      title: criteria.criteria_name,
      weight: criteria.weight_percentage,
      description: criteria.criteria_description,
      type: criteria.evaluation_type,
      required: criteria.is_mandatory,
      min_word_count: criteria.min_word_count,
      min_score: criteria.min_score,
      max_score: criteria.max_score,
      veto_enabled: criteria.veto_enabled,
      veto_threshold: criteria.veto_threshold
    }))
    
    // Initialize responses object with criteria IDs
    responses.value = {}
    evaluationCriteria.value.forEach(criteria => {
      responses.value[criteria.id] = ''
    })
    
    // Load saved draft if exists
    if (data.saved_draft) {
      try {
        const draftData = data.saved_draft
        if (draftData.responses) {
          responses.value = { ...responses.value, ...draftData.responses }
        }
        if (draftData.documents) {
          documents.value = draftData.documents
        }
        if (draftData.key_personnel) {
          keyPersonnel.value = draftData.key_personnel
        }
        console.log('Loaded saved draft from API:', draftData)
      } catch (e) {
        console.warn('Error loading draft from API:', e)
      }
    }
    
    // Update completion status
    if (data.submission_status) {
      completionStatus.value = {
        company: formData.value.companyName ? 100 : 0,
        responses: data.submission_status.completion_percentage || 0,
        documents: documents.value.filter(d => d.uploaded).length / documents.value.length * 100,
        personnel: keyPersonnel.value.length > 0 ? 100 : 0
      }
    }
    
    // Check if submission deadline has passed
    if (rfp.is_deadline_passed && !rfp.allow_late_submissions) {
      error.value = 'Submission deadline has passed. Late submissions are not allowed.'
      return
    }
    
  } catch (err) {
    console.error('Error loading RFP data:', err)
    error.value = 'Failed to load RFP data. Please check your link and try again.'
  } finally {
    loading.value = false
  }
})

const handleSubmit = async () => {
  try {
    if (overallProgress.value < 90) {
      showError("Submission incomplete", "Please complete all required sections before submitting.")
      return
    }

    const token = props.token
    if (!token) {
      showError("Invalid token", "Please use a valid invitation link.")
      return
    }

    // Save submission data
    const submissionData = {
      vendor_info: formData.value,
      responses: responses.value,
      documents: documents,
      key_personnel: keyPersonnel.value,
      submitted_at: new Date().toISOString()
    }

    // Save to localStorage first (backup)
    localStorage.setItem('rfp_submitted_' + token, JSON.stringify(submissionData))

    // Try to submit to API
    const response = await fetch(`/api/v1/rfp-responses/submit/${token}/`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(submissionData)
    })

    if (!response.ok) {
      throw new Error('Failed to submit proposal')
    }

    success("Proposal submitted successfully", "Your proposal has been submitted for evaluation.")

    // Disable form after successful submission
    document.querySelectorAll('textarea, input').forEach(input => {
      if (!input.readOnly) input.disabled = true
    })
    document.querySelectorAll('button').forEach(btn => {
      btn.disabled = true
    })

  } catch (err) {
    console.error('Error submitting proposal:', err)
    showError("Submission failed", "Please try again or contact support.")
  }
}
</script>

